import EventsState from "@/store/modules/Events/state";
import { MutationTree } from "vuex";
import { IEvent, IEventsState } from "@/store/modules/Events/types";

/**
 * @param {IEventsState} state
 * @param {IEvent} event
 */
export function PUSH_EVENT_TODO(state: IEventsState, event: IEvent): void {
    state.todo.push(event);
}

export const EventsMutations: MutationTree<EventsState> = {
    /**
     * @param {IEventsState} state
     * @param {IEvent} event
     */
    PUSH_EVENT_TODO
}

export default EventsMutations;
