import { Module } from "Vuex";
import { IRootState } from "@/store/types";
import CalendarState from "@/store/modules/Calendar/state";
import CalendarMutations from "@/store/modules/Calendar/mutations";
import CalendarGetters from "@/store/modules/Calendar/getters";

export const CalendarStore: Module<CalendarState, IRootState> = {
    namespaced: true,
    state: new CalendarState(),
    mutations: CalendarMutations,
    getters: CalendarGetters
};

export default CalendarStore;
