import { ICalendarState } from "@/store/modules/Calendar/types";
import {getDaysInMonth, getMonth, getYear, getDay, format} from "date-fns";

class CalendarState implements ICalendarState {
    date: Date = new Date();
    month: number = getMonth(this.date);
    year: number = getYear(this.date);
    monthDays: number = getDaysInMonth(this.date);
    firstDayOfWeek: number = getDay(new Date().setDate(1));
    activeDay: number | null = parseInt(format(this.date, "d"));
}

export default CalendarState;
