import { Component, Vue } from 'vue-property-decorator';

import global from "@/assets/modules/global.css?module";
import grid from "@/assets/modules/grid.css?module";

import ComponentCalendar from "@/components/ComponentCalendar/ComponentCalendar";
import ComponentEvents from "@/components/ComponentEvents/ComponentEvents";

@Component
export default class App extends Vue {
  render() {
    return (
      <div id="app" >
        <div class={global.container}>
            <div class={global.wrapper}>
                <div class={grid.root}>
                    <div class={grid['col-6']}>
                        <ComponentCalendar />
                    </div>
                    <div class={grid['col-6']}>
                        <ComponentEvents />
                    </div>
                </div>
            </div>
        </div>
      </div>
    )
  }
}
