import { Component, Vue, Prop } from 'vue-property-decorator';

import uIComponentCheckboxCss from "@/components/UI/UIComponentCheckbox/UIComponentCheckbox.css?module";

@Component
export default class UIComponentCheckbox extends Vue {
    @Prop({ default: false }) readonly value?: boolean
    @Prop(String) readonly label?: string | null

    onHandlerInput(event: any) {
        this.$emit("input", !this.value)
    }

    render() {
        return (
            <div class={uIComponentCheckboxCss.root} onclick={this.onHandlerInput}>
                <input
                    type="checkbox"
                    checked={this.value}
                    class={uIComponentCheckboxCss.input}
                />
                <span class={uIComponentCheckboxCss.label}>
                    {this.label}
                </span>
            </div>
        )
    }
}
