import { Module } from "Vuex";
import { IRootState } from "@/store/types";
import EventsState from "@/store/modules/Events/state";
import EventsMutations from "@/store/modules/Events/mutations";
import EventsGetters from "@/store/modules/Events/getters";

export const EventsStore: Module<EventsState, IRootState> = {
    namespaced: true,
    state: new EventsState(),
    mutations: EventsMutations,
    getters: EventsGetters
};

export default EventsStore;
