import { Component, Vue } from 'vue-property-decorator';

import card from "@/assets/modules/card.css?module";

import componentCalendarCss from "@/components/ComponentCalendar/ComponentCalendar.css?module";
import ComponentCalendarDay from "@/components/ComponentCalendar/ComponentCalendarDay";

@Component
export default class ComponentCalendar extends Vue {
    get year() {
        return this.$store.state.Calendar.year;
    }

    get monthDays() {
        return this.$store.state.Calendar.monthDays;
    }

    get firstDayOfWeek() {
        return this.$store.state.Calendar.firstDayOfWeek;
    }

    get monthName(): string {
        return this.$store.getters["Calendar/getMonthName"];
    }

    /**
     * Возвращает сетку, для отображения дней, с отступом
     */
    get gridDayList(): number[] {
        const start = this.firstDayOfWeek - 1;
        const maxDays = this.monthDays + start;
        return Array.from(Array(maxDays).keys());
    }

    render() {
        const nameDayOfWeekList = [ "Пн", "Вт", "Ср", "Чт", "Пт", "Сб", "Вс" ].map((_v: string) => {
            return (
                <div class={componentCalendarCss.dayOfWeek}>
                    <span>{_v}</span>
                </div>
            )
        });

        const dayOfWeekList = this.gridDayList.map((dayName: number) => {
            return (
                <ComponentCalendarDay day={dayName - this.firstDayOfWeek + 2} />
            )
        });

        return (
            <div class={[card.root, componentCalendarCss.root]} >
                <span class={card.title}>
                    {`${this.monthName} ${this.year}`}
                </span>

                <div class={componentCalendarCss.days}>
                    {nameDayOfWeekList}
                    {dayOfWeekList}
                </div>
            </div>
        )
    }
}
