import { Component, Vue } from 'vue-property-decorator';

import card from "@/assets/modules/card.css?module";
import componentEventsCss from "@/components/ComponentEvents/ComponentEvents.css?module";

import UIComponentInput from "@/components/UI/UIComponentInput/UIComponentInput";
import UIComponentCheckbox from "@/components/UI/UIComponentCheckbox/UIComponentCheckbox";
import {IEvent} from "@/store/modules/Events/types";

@Component
export default class ComponentEvents extends Vue {
    todoText: string | null = null;

    get year() {
        return this.$store.state.Calendar.year;
    }

    get month() {
        return this.$store.state.Calendar.month;
    }

    get activeDay() {
        return this.$store.state.Calendar.activeDay;
    }

    get todo() {
        return this.$store.getters["Events/getTodoByDaY"](this.activeDay);
    }

    createTodoEvent() {
        this.$store.commit("Events/PUSH_EVENT_TODO", {
            id: `${this.year}${this.month}${this.activeDay}`,
            day: this.activeDay,
            mount: this.month,
            text: this.todoText,
            success: false
        });
    }

    render() {
        const eventsList = this.todo.map((todo: IEvent) => {
            return (
                <div class={componentEventsCss.list}>
                    <UIComponentCheckbox
                        v-model={todo.success}
                        label={todo.text}
                    />
                </div>
            )
        });

        return (
            <div class={[card.root, componentEventsCss.root]} >
                <span class={[card.title, card.title_2]}>
                    События
                </span>

                {eventsList}

                <p style="marginLeft: 33px">
                    <UIComponentInput v-model={this.todoText} oninput={this.createTodoEvent}/>
                </p>
            </div>
        )
    }
}
