import {GetterTree} from "vuex";
import { IRootState } from "@/store/types";
import EventsState from "@/store/modules/Events/state";
import {IEvent, IEventsState} from "@/store/modules/Events/types";

/**
 * @param {IEventsState} state
 * @return {Function(IEvent[])}
 */
export function getTodoByDaY(state: IEventsState): Function {
    return function (day: number): IEvent[] {
        return state.todo.filter((event:IEvent) => event.day === day)
    }
}

export const EventsGetters: GetterTree<EventsState, IRootState> = {
    /**
     * @param {IEventsState} state
     * @return {Function(IEvent[])}
     */
    getTodoByDaY
}

export default EventsGetters;
