import { Component, Vue, Prop } from 'vue-property-decorator';
import ComponentCalendarCss from "@/components/ComponentCalendar/ComponentCalendar.css?module";

@Component
export default class ComponentCalendarDay extends Vue {
    @Prop(Number) readonly day?: number | null

    get dayName(): number | null {
        return this.day && this.day > 0 ? this.day : null
    }

    get activeDay() {
        return this.$store.state.Calendar.activeDay;
    }

    get todo() {
        return this.$store.getters["Events/getTodoByDaY"](this.day);
    }

    get cssClass(): string[] {
        let cssClass = [];

        if (this.activeDay === this.day) {
            cssClass.push(ComponentCalendarCss.dayActive)
        }

        if (this.todo.length > 0) {
            cssClass.push(ComponentCalendarCss.colorRed)
        }

        return cssClass;
    }

    setActiveDay() {
        this.$store.commit("Calendar/SET_VALUE_ACTIVE_DAY", this.day);
    }

    render() {
        return (
            <div class={ComponentCalendarCss.day} onclick={this.setActiveDay}>
                <span class={this.cssClass}>
                    {this.dayName}
                </span>
            </div>
        )
    }
}
