import CalendarState from "@/store/modules/Calendar/state";
import { MutationTree } from "vuex";
import { ICalendarState } from "@/store/modules/Calendar/types";

/**
 * @param {ICalendarState} state
 * @param {string} value
 */
export function SET_VALUE_ACTIVE_DAY(state: ICalendarState, value: number | null): void {
    state.activeDay = value;
}

export const CalendarMutations: MutationTree<CalendarState> = {
    /**
     * @param {ICalendarState} state
     * @param {string} value
     */
    SET_VALUE_ACTIVE_DAY
}

export default CalendarMutations;
