import {ICalendarState} from "@/store/modules/Calendar/types";
import {GetterTree} from "vuex";
import CalendarState from "@/store/modules/Calendar/state";
import { IRootState } from "@/store/types";

/**
 * @param {ICalendarState} state
 */
export function getMonthName(state: ICalendarState): string {
    const monthNames: string[] = [
        "Январь", "Февраль", "Март", "Апрель", "Май", "Июнь",
        "Июль", "Август", "Сентябрь", "Ноябрь", "Декабрь",
    ];

    return monthNames[state.month];
}

export const CalendarGetters: GetterTree<CalendarState, IRootState> = {
    /**
     * @param {ICalendarState} state
     */
    getMonthName
}

export default CalendarGetters;
