import { Component, Vue, Prop } from 'vue-property-decorator';

import uIComponentInputCss from "@/components/UI/UIComponentInput/UIComponentInput.css?module";

@Component
export default class UIComponentInput extends Vue {
    @Prop(String) readonly value?: string | null

    text?: string | null = this.value || null;

    onHandlerInput(event: any): void {
        let { value } = event.target;
        value = value ? value.trim() : null;
        this.text = value;
    }

    onHandlerKeyup(event: any) {
        let { keyCode } = event;
        if (keyCode === 13) {
            this.$emit("input", this.text);
            this.text = null;
        }
    }

    render() {
        return (
            <input
                class={uIComponentInputCss.root}
                placeholder="Текст"
                type="text"
                value={this.text}
                oninput={this.onHandlerInput}
                onkeyup={this.onHandlerKeyup}
            />
        )
    }
}
